<template>
  <div id="demo_chart"></div>
</template>

<script>
import Highcharts from "highcharts/highstock";
import Api from "../Api";
import sma from "../indicators/sma";

let code = "002581";
let ma5;
function toPoint(item) {
  let ser = [];
let vol = {};
  ser.push(item[0]);
  ser.push(item[1]);
  ser.push(item[2]);
  ser.push(item[3]);
  ser.push(item[4]);
  if (item[1] >= item[4]) {
    vol.color = "#00b746";
  } else {
    vol.color = "#ef403c";
  }
  vol.x=item[0];
  vol.y=item[5];
  return { data: ser, volume: vol };
}

export default {
  name: "Demo",
  data: function() {
    return {
      timer: undefined,
      chart: undefined,
      data: [],
      index: 0
    };
  },
  mounted() {
    Highcharts.setOptions({
      lang: {
        rangeSelectorZoom: ""
      }
    });
    Api.query(code)
      .then(datas => {
        let initData = [];
        let initVol = [];
        let initM5 = [];
        for (let i = 0; i < 100; i++, this.index++) {
          let item = datas[i];
          if (i < 4) {
            ma5 = sma(i + 1, Api.getClose(item), ma5);
          } else {
            ma5 = sma(5, Api.getClose(item), ma5);
          }
          let d = toPoint(item);
          initData.push(d.data);
          initVol.push(d.volume);
          initM5.push({x: Api.getDate(item), y:ma5});
        }
        this.data = datas;
        this.draw(initData, initVol, initM5);
        this.timer = setInterval(() => {
          if (this.index < this.data.length) {
            let data = this.data[this.index];
            this.index++;
            let point = toPoint(data);
            ma5 = sma(5, Api.getClose(data), ma5);
            this.chart.series[0].addPoint(point.data, false, true);
            this.chart.series[1].addPoint(point.volume, false, true);
            this.chart.series[2].addPoint({x: Api.getDate(data), y:ma5}, false, true);
            this.chart.redraw();
          } else {
            clearInterval(this.timer);
            this.timer = null;
          }
        }, 2000);
      })
      .catch(err => console.log(err));
  },
  beforeDestroy() {
    if (this.timer) {
      clearInterval(this.timer);
    }
  },
  methods: {
    draw(data, volume, m5) {
      this.chart = Highcharts.stockChart("demo_chart", {
        rangeSelector: {
          buttons: [
            {
              type: "all",
              text: "全部"
            }
          ],
          buttonTheme: {
            display: "none" // 不显示按钮
          },
          selected: 0,
          inputEnabled: false
        },
        scrollbar: {
          enabled: false
        },
        credits: {
          enabled: false
        },
        navigator: {
          enabled: false
        },
        exporting: {
          enabled: false
        },
        series: [
          {
            type: "candlestick",
            data: data,
            name: "candlestick",
            id: "candlestick",
            zIndex: 2
          },
          {
            type: "column",
            name: "Volume",
            data: volume,
            yAxis: 1
          },
          {
            type: "spline",
            name: "5日均线",
            linkedTo: "candlestick",
            zIndex: 1,
            data: m5,
            colorIndex: 0,
            lineWidth: 1
          }
        ],
        yAxis: [
          {
            startOnTick: false,
            endOnTick: false,
            labels: {
              align: "left",
              x: -3
            },
            title: {
              text: "股价"
            },
            height: "75%",
            lineWidth: 2,
            resize: {
              enabled: true
            }
          },
          {
            labels: {
              align: "left",
              x: -3
            },
            title: {
              text: "交易量"
            },
            top: "75%",
            height: "25%",
            offset: 0,
            lineWidth: 2
          }
        ],

        xAxis: {
          type: "datetime",
          dateTimeLabelFormats: {
            second: "%Y-%m-%d<br/>%H:%M:%S",
            minute: "%Y-%m-%d<br/>%H:%M",
            hour: "%Y-%m-%d<br/>%H:%M",
            day: "%Y<br/>%m-%d",
            week: "%Y<br/>%m-%d",
            month: "%Y-%m",
            year: "%Y"
          }
        }
      });
    }
  }
};
</script>

<style>
.highcharts-point-up {
  fill: #ef403c;
  stroke: #ef403c;
}
.highcharts-point-down {
  fill: #00b746;
  stroke: #00b746;
}
</style>