import Latics from './Latics'
import Api from '../Api'

export default class UP10BS extends Latics{
    constructor(){
        super('up10 bs')
    }

    buy(data){
        if(this.common(data)){
            return false;
        }
        let rate = Api.getGL(data);
        return rate > 9;
    }

    sale(data){
        if(this.common(data)){
            return false;
        }
        let rate = Api.getGL(data);
        return rate < 5;
    }
}
