/**
 * The Buy or Sales Latics. 
 */
import Api from '../Api'

export default class Latics{
    /**
     * name of Latics.
     * @param {Latics name} name 
     */
    constructor(name){
        this.name = name;
    }

    /**
     * can't buy or sale.
     * @param {data} data 
     */
    common(data){
        return Api.getHigh(data) == Api.getLow(data);
    }


    /**
     * if data can buy than return true, else return false.
     * 
     */
    buy(data){
        return  data && false;
    }

    /**
     * if data can sales than return true, else return false.
     */
    sale(data){
        return data && false;
    }
}