<template>
  <Layout>
    <div class="pure-g">
      <div class="pure-u-1">
        <Chart :series="initData" :data="data"></Chart>
      </div>
      <div class="pure-u-1">
        <div class="pure-g">
          <div class="pure-u-1-3" style="text-align: left;text-indent:20px;">
            <p>统计: <template v-if="isEnd">{{stock.code}},{{stock.name}}</template></p>
            <p>总计： 交易次数 {{count}} 次 持股 {{report.usedTime}} 天</p>
            <p>盈亏: {{sum}}</p>
            <p v-if="isEnd">幅度：{{gl}} 百分比: {{glRate * 100}} %</p>
          </div>
          <div class="pure-u-1-3" style="text-align: left;text-indent:20px;">
            <p>操作日志</p>
            <p v-for="(item, index) in log" :key="index" style="line-height: 14px">{{item}}</p>
          </div>
          <div class="pure-u-1-3">
            <button @click="buy" :class="buyClass">买入</button>
            <button @click="sale" :class="saleClass">卖出</button>
            <button @click="start" :class="startClass">开始</button>
            <button @click="next" :class="nextClass">下一个</button>
          </div>
        </div>
      </div>
    </div>
  </Layout>
</template>

<script>
import Layout from "./Layout";
import Chart from "./Chart";
import Report from "../Report";
import config from "../Config";
import Api from "../Api";

let stockList = config.stockList;

let initSize = config.chart.initSize;
const internal = config.chart.internal;
let int;
export default {
  name: "mock",
  components: {
    Layout,
    Chart
  },
  data: function() {
    return {
      series: [],
      log: [],
      report: new Report(),
      number: 0,
      stock: {},
      isEnd: false,
      isStart: false,
      have: false,
      initData: [],
      data: [],
      index: 0
    };
  },
  computed: {
    nextClass() {
      return {
        "pure-button-disabled": this.isStart,
        "stock-button": true,
        "pure-button": true
      };
    },
    startClass() {
      return {
        "pure-button-disabled": this.isStart || this.isEnd,
        "stock-button": true,
        "pure-button": true
      };
    },
    buyClass() {
      return {
        "pure-button-disabled": this.isEnd || !this.isStart || this.have,
        "stock-button": true,
        "pure-button": true
      };
    },
    saleClass() {
      return {
        "pure-button-disabled": this.isEnd || !this.isStart || !this.have,
        "stock-button": true,
        "pure-button": true
      };
    },
    sum() {
      return this.report.sum;
    },
    count() {
      return this.report.count.up + this.report.count.down;
    },
    gl() {
      return (
        this.series[this.series.length - 1][4] - this.series[0][4]
      ).toFixed(2);
    },
    glRate() {
      return (this.gl / this.series[0][4]).toFixed(2);
    }
  },
  methods: {
    buy() {
      this.report.buy(this.data);
      this.have = true;
    },
    sale() {
      this.report.sale(this.data);
      this.have = false;
    },
    start() {
      this.isStart = true;
      int = setInterval(() => {

        let d = this.series[this.index];
        this.data = d; //add data to chart. The chart component well watch the data.
        this.index++;
        if (this.index >= this.series.length) {
          this.index = 0;
          this.isEnd = true;
          this.isStart = false;
          if (this.have) {
        this.report.sale(this.data);
      }
      this.have = false;
          clearInterval(int);
        }
      }, internal);
    },
    beforeDestroy() {
      if (int) {
        clearInterval(int);
      }
    },
    next() {
      if(this.number == stockList.length){
        return;
      }
      localStorage.setItem('stock-number', this.number);
      this.stock = stockList[this.number];
      console.log(this.stock);
      this.report = new Report(this.stock, event => {
        if (this.log.length == 3) {
          this.log.shift();
        }
        this.log.push("b: " + event.buyPrice + " s: " + event.salePrice);
      });
      this.log = [];
      Api.query(this.stock.code)
        .then(resp => {
          this.series = resp;
          if (initSize == 0 || initSize > resp.length) {
            initSize = resp.length;
          }
          let arr = [];
          for (this.index = 0; this.index < initSize; this.index++) {
            arr.push(resp[this.index]);
          }
          this.initData = arr;
        })
        .catch(err => console.log(err));
      this.number++;
      this.isEnd = false;
    }
  },
  mounted() {
    this.number = localStorage.getItem('stock-number');
    if(this.number === null){
      this.number = 0;
      localStorage.setItem('stock-number', this.number);
    }

    this.next();
  }
};
</script>

<style>
.stock-button {
  margin: 4px;
}
</style>