<template>
  <Layout>
    <div class="pure-g">
      <div class="pure-u-1">
        <Chart :series="data" :flags="flags"></Chart>
      </div>
      <div class="pure-u-1">
        <div class="pure-g">
          <div class="pure-u-1-3" style="text-align: left;text-indent:20px;">
            <p>统计: {{stock.code}} {{stock.name}}</p>
            <p>sum {{ report.sum }}</p>
          </div>
          <div class="pure-u-1-3" style="text-align: left;text-indent:20px;"></div>
          <div class="pure-u-1-3">
            <button @click="next" :class="{'pure-button-disabled': end,'pure-button': true, 'stock-button':true}">下一个</button>
          </div>
        </div>
      </div>
    </div>
  </Layout>
</template>

<script>
import Layout from "./Layout";
import Api from "../Api";
import stockList from "../selected.json";
import config from "../Config";
import Report from "../Report";
import Chart from './Chart'
export default {
  name: "analytic",
  components: {
    Layout,
    Chart
  },
  data: function() {
    return {
      index: 0,
      report: {},
      stock: {},
      data: [],
      flags: [],
      end: false
    };
  },
  mounted() {
    this.next();
  },
  methods: {
    next() {
      if(this.index == stockList.length){
        this.end = true;
      }else{
        this.stock = stockList[this.index];
      this.report = new Report(this.stock);
      Api.query(this.stock.code)
        .then(resp => {
          this.data = resp;
          let latics = new config.latics();
          let have = false;
          let flags = [];
          for (let i = 0; i < resp.length; i++) {
            const item = resp[i];
            if (have == false) {
              if (latics.buy(item)) {
                have = true;
                this.report.buy(item);
                flags.push({ x: item[0], title: "B" });
              }
            } else {
              if (latics.sale(item)) {
                have = false;
                this.report.sale(item);
                flags.push({ x: item[0], title: "S" });
              }
            }
          }
          this.flags = flags;
          this.index++;
        })
        .catch(err => console.log(err));
      }
    }
  }
};
</script>

<style>
.stock-button {
  margin: 4px;
}
</style>