import axios from 'axios'
import stockList from './stock-list.json';

function format(arr){
    let data = [];
    for (let i = arr.length - 1; i >= 0; i--) {
        const item = arr[i];

        let obj = [];
        obj.push(Date.parse(item[0])); //date.
        obj.push(parseFloat(item[1])); // open
        obj.push(parseFloat(item[6])); //high
        obj.push(parseFloat(item[5])); //low
        obj.push(parseFloat(item[2])); //close
        obj.push(parseInt(item[7])); //volume
        obj.push(parseFloat(item[4])); //grant or loss rate.
        data.push(obj);
    }
    return data;
}
function toStockData(item) {
    let ser = [];
    let vol = {};
    ser.push(item[0]);
    ser.push(item[1]);
    ser.push(item[2]);
    ser.push(item[3]);
    ser.push(item[4]);
    vol.x = item[0];
    vol.y = item[5];
    if (item[1] >= item[4]) {
      vol.color = "#00b746";
    } else {
      vol.color = "#ef403c";
    }
    return { data: ser, volume: vol };
  }

  function doQuery(url){
    return new Promise((resolve, reject) => {
        axios.get(url)
        .then(resp => {
            let data = resp.data[0];
            resolve(format(data.hq));
        }).catch(err => reject(err));
    })
  }

function query(code){
    return doQuery('/data/' + code);
}

function queryOld(code){
    return doQuery('/1415/' + code);
}

function getOpen(data){
    return data[1];
}
function getClose(data){
    return data[4];
}
function getDate(data){
    return data[0];
}
function getHigh(data){
    return data[2];
}
function getLow(data){
    return data[3];
}
function getGL(data){
    return data[6];
}
export default {
    query,
    format,
    stockList,
    getClose,
    getOpen,
    getHigh,
    getLow,
    getDate,
    toStockData,
    getGL,
    queryOld
}
