/**
 * general report.
 */
import config from './Config'

export default class Report {
    constructor(item, lisener) {
        this.item = item;
        this.count = 0;
        this.event = {};
        this.sum = 10000;
        this.log = [];
        this.lisener = lisener;
        this.buyPrice = 0;
        this.one = {};
        this.usedTime = 0;
        this.buyTime = 0;
        this.count = {
            up: 0,
            down: 0
        };
    }

    record(data) {
        if (this.one.open === undefined) {
            this.one.open = data[1];
        }
        this.one.close = data[4];

        if (this.one.high === undefined) {
            this.one.high = data[2];
        } else {
            this.one.high = Math.max(this.one.high, data[2]);
        }

        if (this.one.low === undefined) {
            this.one.low = data[3];
        } else {
            this.one.low = Math.min(this.one.low, data[3]);
        }
    }

    buy(data) {
        let date = data[0], price = data[4];
        this.event.buyDate = date;
        this.event.buyPrice = price;
        this.buyPrice = price;
        this.log.push(this.event);
        this.buyTime = date;
    }

    sale(data) {
        let date = data[0], price = data[4];
        if(price > this.buyPrice){
            this.count.up++;
        }else{
            this.count.down++;
        }
        this.event.saleDate = date;
        this.event.salePrice = price;
        if (this.lisener) {
            this.lisener(this.event);
        }
        this.sum = this.sum * (1 + (price - this.buyPrice)/this.buyPrice);
        this.log.push(this.event);
        this.usedTime = this.usedTime + ((date - this.buyTime)/ 1000 / 60 / 60 / 24);
    }

    report() {
        let rep = Object.assign({}, this.item);
        if (config.isTrace) {
            rep.log = this.log;
        }

        rep.count = this.count;
        rep.sum = this.sum.toFixed(2);
        rep.open = this.one.open;
        rep.close = this.one.close;
        rep.high = this.one.high;
        rep.low = this.one.low;
        rep.usedTime = this.usedTime;
        return rep;
    }
}