<template>
  <div class="chart" id="candlestick_chart"></div>
</template>

<script>
import Highcharts from "highcharts/highstock";
import sma from "../indicators/sma";
import Api from "../Api";

let chart;

Highcharts.setOptions({
  lang: {
    rangeSelectorZoom: ""
  }
});

export default {
  name: "Chart",
  props: {
    data: Array,
    series: Array,
    flags: Array
  },
  data: function() {
    return {
      ma5: 0,
      ma10: 0,
      ma20: 0,
      ma60: 0
    };
  },
  watch: {
    flags: function(val){
      if(val && val.length != 0){
        for (const it of val) {
          chart.series[6].addPoint(it, false, false);
        }
        chart.redraw();
      }
      
    },
    series: function(val) {
      if (val.length != 0) {
        let ss = [];
        let volumes = [];
        let m5 = [];
        let m10 = [];
        let m20 = [];
        let m60 = [];

        for (let i = 0; i < this.series.length; i++) {
          const item = this.series[i];
          let res = Api.toStockData(item);
          volumes.push(res.volume);
          ss.push(res.data);
          const close = Api.getClose(item);
          if (i < 4) {
            this.ma5 = sma(i + 1, close, this.ma5);
          } else {
            this.ma5 = sma(5, close, this.ma5);
          }
          m5.push({ x: Api.getDate(item), y: this.ma5 });
          if (i < 9) {
            this.ma10 = sma(i + 1, close, this.ma10);
          } else {
            this.ma10 = sma(10, close, this.ma10);
          }
          m10.push({ x: Api.getDate(item), y: this.ma10 });
          if (i < 19) {
            this.ma20 = sma(i + 1, close, this.ma20);
          } else {
            this.ma20 = sma(20, close, this.ma20);
          }
          m20.push({ x: Api.getDate(item), y: this.ma20 });
          if (i < 59) {
            this.ma60 = sma(i + 1, close, this.ma60);
          } else {
            this.ma60 = sma(60, close, this.ma60);
          }
          m60.push({ x: Api.getDate(item), y: this.ma60 });
        }
        this.draw(ss, volumes, m5, m10, m20, m60, this.flags);
      }
    },
    data: function(point) {
      let data = Api.toStockData(point);
      let close = Api.getClose(point);
      this.ma5 = sma(5, close, this.ma5);
      this.ma10 = sma(10, close, this.ma10);
      this.ma20 = sma(20, close, this.ma20);
      this.ma60 = sma(60, close, this.ma60);
      chart.series[0].addPoint(data.data, false, true);
      chart.series[1].addPoint(data.volume, false, true);
      chart.series[2].addPoint(
        { x: Api.getDate(point), y: this.ma5 },
        false,
        true
      );
      chart.series[3].addPoint(
        { x: Api.getDate(point), y: this.ma10 },
        false,
        true
      );
      chart.series[4].addPoint(
        { x: Api.getDate(point), y: this.ma20 },
        false,
        true
      );
      chart.series[5].addPoint(
        { x: Api.getDate(point), y: this.ma60 },
        false,
        true
      );
      chart.redraw();
    }
  },
  methods: {
    draw(data, volume, ma5, ma10, ma20, ma60, flags){
      chart = Highcharts.stockChart("candlestick_chart", {
      title: {
        enabled: false
      },
      rangeSelector: {
        inputDateFormat: "%Y-%m-%d",
        buttons: [
          // {
          //   type: 'month',
          //   text: '3个月',
          //   count: '3'
          // },
          // {
          //   type: 'month',
          //   text: '半年',
          //   count: '6'
          // },
          // {
          //   type: 'year',
          //   text: '一年',
          //   count: '1'
          // },
          {
            type: "all",
            text: "全部"
          }
        ],
        buttonTheme: {
          display: "none" // 不显示按钮
        },
        selected: 0,
        inputEnabled: false
      },
      scrollbar: {
        enabled: false
      },
      credits: {
        enabled: false
      },
      navigator: {
        enabled: false
      },
      tooltip: {
        enabled: false
      },
      series: [
        {
          type: "candlestick",
          data: data,
          name: "candlestick",
          id: "candlestick",
          zIndex: 2
        },
        {
          type: "column",
          name: "Volume",
          data: volume,
          yAxis: 1
        },
        {
          type: "spline",
          name: "5日均线",
          linkedTo: "candlestick",
          zIndex: 1,
          data: ma5,
          colorIndex: 0,
          lineWidth: 1
        },
        {
          type: "spline",
          name: "10日均线",
          linkedTo: "candlestick",
          zIndex: 1,
          data: ma10,
          colorIndex: 1,
          lineWidth: 1
        },
        {
          type: "spline",
          name: "20日均线",
          linkedTo: "candlestick",
          zIndex: 1,
          data: ma20,
          colorIndex: 2,
          lineWidth: 1
        },
        {
          type: "spline",
          name: "60日均线",
          linkedTo: "candlestick",
          zIndex: 1,
          data: ma60,
          colorIndex: 3,
          lineWidth: 1
        },
        {
            type: "flags",
            data: flags,
            linkedTo: "candlestick",
            onSeries: "candlestick",
            shape: "circlepin",
            width: 16,
            zIndex: 10
          }
      ],
      yAxis: [
        {
          startOnTick: false,
          endOnTick: false,
          labels: {
            align: "right",
            x: -3
          },
          title: {
            text: "股价"
          },
          height: "75%",
          lineWidth: 2,
          resize: {
            enabled: true
          }
        },
        {
          labels: {
            align: "right",
            x: -3
          },
          title: {
            text: "交易量"
          },
          top: "75%",
          height: "25%",
          offset: 0,
          lineWidth: 2
        }
      ],
      xAxis: {
        type: "datetime",
        dateTimeLabelFormats: {
          second: "%Y-%m-%d<br/>%H:%M:%S",
          minute: "%Y-%m-%d<br/>%H:%M",
          hour: "%Y-%m-%d<br/>%H:%M",
          day: "%Y<br/>%m-%d",
          week: "%Y<br/>%m-%d",
          month: "%Y-%m",
          year: "%Y"
        }
      }
    });
    }
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.chart {
  height: 70vh;
}
.highcharts-point-up {
  fill: #ef403c;
  stroke: #ef403c;
}
.highcharts-point-down {
  fill: #00b746;
  stroke: #00b746;
}
</style>
